<template>
  <v-app-bar
    id="topo"
    clipped-left
    app
    color="primary"
    dark
  >
    <v-btn text @click="toggleMenu()">
      <v-icon>
        menu
      </v-icon> 
    </v-btn>

    <v-toolbar-title class="ml-4">
      {{ title }}
      <small v-if="subtitle" class="d-none d-md-inline barbershopname ml-2">
        {{ subtitle }}
      </small>
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <v-badge
      color="primaryLight"
      :content="notificationItems"
      :value="notificationItems > 0"
      overlap
    >
      <div class="notification-icon" @click="goTo('admin-approval')">
        <v-icon class="d-inline">
          notifications
        </v-icon>
      </div>
    </v-badge>

    <v-divider
      inset
      class="mt-2 mr-6 ml-6 mb-4 d-none d-md-inline"
      vertical
    ></v-divider>
      
    <v-menu
      class="btn-avatar"
      transition="slide-y-transition"
      bottom
    >
      <template v-slot:activator="{ on }">
        <div
          class="btn-avatar"
          dark
          v-on="on"
        >
          <v-avatar
            v-if="avatar"
            size="32px"
            item
          >
            <v-img
              :src="avatar"
              alt="Vuetify"
            >
            </v-img>
          </v-avatar>

          <v-icon v-if="!avatar" class="d-none d-md-inline">
            account_circle
          </v-icon>

          <span class="ml-2 d-none d-md-inline">
            {{ name }}
          </span>

          <v-icon class="ml-2 d-none d-md-inline">
            keyboard_arrow_down
          </v-icon>

          <v-icon class="ml-2 d-inline d-sm-inline d-md-none">
            more_vert
          </v-icon>
        </div>
      </template>
      <v-list>
        <v-list-item
          v-for="(item, i) in items"
          :key="i"
          @click="clickMenu(item)"
        >
          <v-list-item-title>
            {{ item.title }}
          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </v-app-bar>
</template>

<script>
export default {
  name: 'Topo',
  props: {
    avatar: { default: null },
    items: { default: Array },
    name: { default: null },
    notificationItems: { default: Array },
    title: { default: 'Meu Barbeiro' },
    subtitle: { default: null }
  },
  methods: {
    toggleMenu () {
      this.$emit('toggleMenu')
    },
    clickMenu (item) {
      this.$emit('clicked', item)
    }
  }
}
</script>

<style lang="scss" scoped>
#topo { 
  .btn-avatar { cursor: pointer; }
  .barbershopname { font-size: 12px; }
  .notification-icon { cursor: pointer; }
}
</style>