<template>
  <v-navigation-drawer
    id="menu-lateral"
    color="primaryDark"
    :class="fullDrawer ? 'open' : ''"
    v-model="drawer"
    clipped
    :permanent="isPermanent"
    fixed
    app
  >
    <v-list dense dark>
      <template v-for="item in itemsMenu">
        <v-list-item
          class="btn-lateral my-1"
          :class="isActive(item.alias)"
          :key="item.text"
          @click="goTo(item.routeName)"
          rounded
          dark
        >
          <v-list-item-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>
              {{ item.text }}
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </template>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import BarberMixin from '@/mixins/BarberMixin'
import BarbershopMixin from '@/mixins/BarbershopMixin'
import MenuMixin from '@/mixins/MenuMixin'
import NavigationMixin from '@/mixins/NavigationMixin'

export default {
  name: 'NavigationDrawer',
  mixins: [ BarberMixin, BarbershopMixin, MenuMixin, NavigationMixin ],
  props: {
    alias: { default: '' },
    itemsMenu: { default: Array }
  },
  data: () => ({
    isPermanent: true,
    drawer: false,
    fullDrawer: false
  }),
  watch: {
    drawer (menu) {
      if (menu) {
        var self = this
        setTimeout(() => {
          var overlay = document.querySelector('.v-overlay__scrim')
          if (overlay) {
            overlay.addEventListener('click', function () {
              self.toggleNavigationDrawer()
            })
          }
        }, 100)
      }
    }
  },
  mounted () {
    this.fullDrawer = this.getDrawer()
    this.isPermanent = window.innerWidth > 959 ? true : false
  },
  methods: {
    isActive (itemAlias) {
      return this.alias === itemAlias ? 'active' : ''
    },
    toggleNavigationDrawer () {
      this.drawer = !this.drawer
      this.fullDrawer = !this.fullDrawer
      this.setDrawer(this.fullDrawer)
    }
  }
}
</script>

<style>
#menu-lateral .v-list { background-color: transparent !important; } 
</style>

<style lang="scss" scoped>
@import 'src/scss/theme.scss';
#menu-lateral { padding: 0 20px; }
#menu-lateral .app-logo { margin: 30px auto; max-width: 100%; display: block; }
#menu-lateral {
  .logo { cursor: pointer; }
  .btn-lateral { border-radius: 2px;
    &:hover, &.active {
      color: $secondary !important;
      background-color: transparent !important;
      opacity: 1;
      &:before { background-color: #fff !important; opacity: 0.1 !important; }
      i { color: $secondary !important; }
    }
  }
}
@media all and (min-width: 960px) {
  #menu-lateral { width: 100px !important; padding: 0 15px; transition: all .5s; 
    &.open { width: 250px !important; }
  }
}
</style>

<style>
#menu-lateral  .v-list-item__action { margin-left: 6px; }
</style>