<template>
  <v-app id="moderators-template">
    <navigation-drawer
      ref="navigationDrawer"
      :alias="alias"
      :itemsMenu="itemsMenu"
    />
    <topo
      name="Nonoca"
      :title="pageName"
      :items="topMenuItems"
      @toggleMenu="toggleMenu"
      @clicked="onClickedTopMenu"
    />
    <v-content class="background main">
      <v-container fluid >
        <breadcrumb
          v-if="breadcrumbs.length > 0"
          :items="breadcrumbs"
          class="mb-8 mt-4 py-0 d-none d-md-block" 
        />
        <div class="conteudo">
          <router-view></router-view>
        </div>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import Breadcrumb from '@/components/globais/breadcrumb/Breadcrumb'
import NavigationDrawer from '@/components/admin/NavigationDrawer'
import Topo from '@/components/admin/topo/Topo'
import NavigationMixin from '@/mixins/NavigationMixin'

export default {
  name: 'Base',
  components: { Breadcrumb, NavigationDrawer, Topo },
  mixins: [ NavigationMixin ],
  data: () => ({
    itemsMenu: Array,
    topMenuItems: [{ title: 'Sair', alias: 'logout' }]
  }),
  computed: {
    alias () {
      return this.$route.meta.alias
    },
    breadcrumbs () {
      return this.$route.meta.breadcrumbs || []
    },
    pageName () {
      return this.$route.meta.pageName
    },
    cssClass () { 
      return 'px-4 px-sm-8' 
    }
  },
  created () {
    const token = this.getToken()
    if (token.usuario.is_moderator) this.setNavigationDrawer()
    else this.logout()
  },  
  methods: {
    getToken () {
      if (localStorage.moderatorToken) {
        const partesToken = localStorage.moderatorToken.split('.')
        if (partesToken.length === 3) {
          try {
            return JSON.parse(atob(partesToken[1]))
          } catch (e) {
            return null
          }
      }
      }
      return null
    },
    logout () {
      localStorage.moderatorToken = null
      this.goTo('moderators-login')
    },
    onClickedTopMenu () {
      this.logout()
    },
    setNavigationDrawer () {
      this.itemsMenu = [
        {
          icon: 'storefront',
          text: 'Barbearias',
          routeName: 'moderators-barbershops',
          alias: 'barbershops'
        },
        {
          icon: 'people_alt',
          text: 'Barbeiros',
          routeName: 'moderators-barbers',
          alias: 'barbers'
        },
        {
          icon: 'people_alt',
          text: 'Usuários',
          routeName: 'moderators-users',
          alias: 'users'
        }
      ] 
    },
    toggleMenu () {
      this.$refs.navigationDrawer.toggleNavigationDrawer()
    }
  }
}
</script>

<style lang="scss" scoped>
@import 'src/scss/theme.scss';

.conteudo { max-width: 1200px; margin: auto; }
.v-list { background: transparent !important; }
.admin-register-content { height: calc(100vh - 150px);
  .btn-register { width: auto; }
}
@media all and (min-width: 960px) {
  nav#menu-lateral + header + .main,
  nav#menu-lateral ~ .app-footer {
    padding-left: 100px !important;
    @include transition-mixin (all, .5s);
  }
  nav#menu-lateral + header + .main { padding-top: 64px !important; }
  nav#menu-lateral.open + header + .main,
  nav#menu-lateral.open ~ .app-footer {
    padding-left: 250px !important;
    @include transition-mixin (all, .5s);
  }
  nav#menu-lateral.open + header + .main { padding-top: 64px !important; }
}

@media all and (max-width: 959px) {
  main { padding-top: 40px !important; padding-bottom: 56px !important; }
}
</style>